body {
  margin: 0 auto;
  max-width: 1260px;
}
body h2 {
  font-size: 35px;
  margin: 20px 0;
  text-transform: uppercase;
  color: gray;
  padding-bottom: 5px;
  display: inline-block;
  position: relative;
}
body h2:before {
  display: block;
  content: "";
  height: 4px;
  width: 100%;
  background: gray;
  position: absolute;
  left: 0;
  bottom: 0;
}
body h2 span {
  color: #fc635e;
}
body a.arrowDown {
  font-size: 30px;
  text-decoration: none;
  color: #fc635e;
  border: 1px solid #fc635e;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  border-radius: 50%;
  width: 45px;
  height: 45px;
}
body a.arrowDown:hover {
  background: #fc635e;
  color: white;
}
body a.btn {
  color: #fc635e;
  text-decoration: none;
  font-size: 22px;
  border: 2px solid #db5854;
  padding: 12px 28px;
  border-radius: 8px;
}
body a.btn:hover {
  background: #db5854;
  color: #fff;
}
body .mainText {
  text-align: center;
}
body .mainText h1 {
  text-transform: uppercase;
  font-size: 50px;
  color: white;
}
body .mainText h1 span {
  color: #fc635e;
}
body .mainText p {
  color: white;
  font-size: 22px;
}
body h1.main_header {
  margin: 0;
  text-transform: uppercase;
  font-size: 40px;
}
body h1.main_header span {
  color: #fc635e;
}
body header {
  background: url('../img/background.png') no-repeat;
  display: flex;
  flex-direction: column;
  min-height: 600px;
  width: 100%;
  max-width: 1260px;
  justify-content: space-around;
}
body header nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
body header nav > a {
  color: white;
  font-size: 25px;
  font-weight: bold;
  text-decoration: none;
}
body header nav > a img {
  padding-right: 15px;
  vertical-align: middle;
}
body header nav .logo:hover img {
  opacity: 0.7;
}
body header nav menu {
  list-style-type: none;
  text-transform: uppercase;
  display: flex;
}
body header nav menu li {
  padding-left: 15px;
}
body header nav menu li a {
  color: white;
  font-size: 20px;
  text-decoration: none;
}
body header nav menu li a:hover {
  color: #fc635e;
  border-bottom: 3px solid #fc635e;
  padding-bottom: 5px;
}
body .recive {
  margin-top: 0;
  text-align: center;
  background: white;
  padding: 70px 50px 10px 50px;
  color: #868686;
}
body .recive p {
  font-size: 22px;
  margin-bottom: 50px;
}
body .icons {
  display: flex;
}
body .icons h3 {
  text-decoration: underline;
  text-transform: uppercase;
}
body .icons p {
  font-size: 18px;
}
body .icons div {
  padding: 15px;
}
body .expend {
  padding: 100px 100px 15px 100px;
  background: url(../img/expand.png) no-repeat;
}
body .expend .mainText {
  padding: 50px;
  margin-bottom: 80px;
  border: 15px solid rgba(0, 0, 0, 0.2);
}
body .expend .mainText h1 {
  font-size: 55px;
  margin-bottom: 50px;
}
body .our_process {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 15px;
}
body .our_process .steps {
  display: none;
}
body .our_process h1,
body .our_process p {
  color: gray;
}
body .our_process h1 {
  text-decoration: underline;
}
body .our_process p {
  font-size: 18px;
}
body .our_process .back_process {
  min-height: 500px;
  min-width: auto;
  background-image: url(../img/Our_process.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}
body .wedo1,
body .wedo2,
body .wedo3 {
  padding: 25px;
  background: #eee;
  display: flex;
  flex-direction: column;
  align-items: center;
}
body .wedo1 .container,
body .wedo2 .container,
body .wedo3 .container {
  display: flex;
  justify-content: space-around;
}
body .wedo1 h1,
body .wedo2 h1,
body .wedo3 h1,
body .wedo1 h2,
body .wedo2 h2,
body .wedo3 h2,
body .wedo1 p,
body .wedo2 p,
body .wedo3 p {
  color: gray;
}
body .wedo1 p,
body .wedo2 p,
body .wedo3 p {
  font-size: 18px;
  padding-bottom: 15px;
}
body .wedo1 h2,
body .wedo2 h2,
body .wedo3 h2 {
  text-transform: uppercase;
  border-left: 10px solid #fc635e;
  padding-left: 5px;
}
body .wedo1 .left,
body .wedo2 .left,
body .wedo3 .left {
  max-width: 50%;
}
body .wedo1 .right,
body .wedo2 .right,
body .wedo3 .right {
  max-width: 50%;
}
body .wedo2 {
  text-align: right;
  background: white;
}
body .wedo2 h2 {
  border-left: none;
  border-right: 10px solid #fc635e;
  padding-left: 0px;
  padding-right: 5px;
}
body .wedo2 .left {
  order: 1;
}
body .wedo2 .right {
  order: 2;
}
body .portfolio {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-bottom: 30px;
}
body .portfolio .btn {
  margin: 40px 0;
}
body .portfolio .controls {
  text-align: center;
  letter-spacing: 2px;
  font-size: 25px;
  border-top: 3px solid gray;
  border-bottom: 3px solid gray;
  padding: 15px;
  margin-bottom: 30px;
}
body .portfolio .controls button {
  color: #fc635e;
  background: transparent;
  border: none;
  text-transform: uppercase;
  font-size: 22px;
}
body .portfolio .controls button:hover {
  color: #e44640;
  cursor: pointer;
}
body .portfolio .container-filter {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
body .portfolio .container-filter #ex1 {
  background-image: url(../img/portfolio/1.png);
}
body .portfolio .container-filter #ex2 {
  background-image: url(../img/portfolio/2.png);
}
body .portfolio .container-filter #ex3 {
  background-image: url(../img/portfolio/3.png);
}
body .portfolio .container-filter #ex4 {
  background-image: url(../img/portfolio/4.png);
}
body .portfolio .container-filter #ex5 {
  background-image: url(../img/portfolio/5.png);
}
body .portfolio .container-filter #ex6 {
  background-image: url(../img/portfolio/6.png);
}
body .portfolio .container-filter div {
  background-size: cover;
  background-position: center center;
  margin: 10px;
  width: 250px;
  height: 180px;
}
body .portfolio .container-filter div .fil {
  font-size: 20px;
  text-transform: uppercase;
  color: white;
  font-weight: bold;
  display: none;
  position: relative;
  margin: 0;
  background: url(../img/portfolio/plus.png) rgba(255, 73, 73, 0.75);
  background-position: center;
  background-repeat: no-repeat;
}
body .portfolio .container-filter div .fil p {
  margin: 0;
  padding: 10px;
}
body .portfolio .container-filter div:hover .fil {
  cursor: pointer;
  display: block;
}
body .our_team {
  display: flex;
  flex-direction: column;
  text-align: center;
  background: #eee;
  margin-bottom: 10px;
  padding: 30px;
}
body .our_team .cont {
  display: flex;
  justify-content: space-around;
}
body .our_team .cont .people {
  text-align: center;
  margin-bottom: 15px;
}
body .our_team .cont a {
  color: #414141;
  font-size: 10px;
}
body .our_team .cont a:hover {
  color: #868686;
}
body .our_team .cont h4 {
  color: #414141;
  padding-bottom: 5px;
}
body .our_team .cont h4 span {
  color: #fc635e;
}
body .our_team .cont p {
  color: gray;
}

@media screen and (max-width: 768px) and (min-width: 550px) {
  nav {
    flex-direction: column;
    justify-content: center;
  }
  body .expend {
    padding: 30px 30px 10px 30px;
  }
  body .expend .mainText {
    padding: 50px 0px;
    margin-bottom: 20px;
  }
  body .expend .mainText h1 {
    font-size: 30px;
    margin-bottom: 50px;
  }
}
@media screen and (max-width: 549px) {
  nav {
    padding: 0px 20px;
  }
  menu {
    display: flex;
    flex-direction: column;
  }
  menu li {
    margin-bottom: 10px;
  }
  body header .mainText h1 {
    margin-top: -20px;
    font-size: 40px;
  }
  body header .mainText p {
    font-size: 18px;
    padding: 20px 10px;
  }
  body .expend {
    padding: 10px;
  }
  body .expend .mainText {
    border: none;
    padding: 30px 0px;
    margin-bottom: 20px;
  }
  body .expend .mainText h1 {
    font-size: 25px;
    margin-bottom: 30px;
  }
  body .our_process .steps {
    display: block;
  }
  body .our_process .back_process {
    display: none;
  }
}
@media screen and (max-width: 1260px) {
  .recive {
    padding: 30px 20px;
  }
  .recive h1 {
    font-size: 26px;
  }
  .recive p {
    font-size: 18px;
    margin-bottom: 20px;
  }
  .icons {
    flex-wrap: wrap;
    justify-content: center;
  }
  .icons div {
    max-width: 350px;
  }
  .icons div p {
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 768px) {
  .wedo1,
  .wedo2,
  .wedo3 {
    padding: 5px;
    text-align: center;
  }
  .wedo1 .container .left h3,
  .wedo2 .container .left h3,
  .wedo3 .container .left h3 {
    border: none;
    padding: 0;
  }
  .wedo1 .container,
  .wedo2 .container,
  .wedo3 .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .wedo1 .container img,
  .wedo2 .container img,
  .wedo3 .container img {
    width: 100%;
  }
  .wedo1 .container .left,
  .wedo2 .container .left,
  .wedo3 .container .left {
    order: 2;
    max-width: 100%;
    margin-bottom: 30px;
  }
  .wedo1 .container .right,
  .wedo2 .container .right,
  .wedo2 .container .right {
    order: 1;
    max-width: 100%;
  }
  .wedo1 .container .btn,
  .wedo1 .container .btn,
  .wedo1 .container .btn {
    margin-bottom: 20px;
  }
}
